
import {mapState,mapGetters,mapMutations} from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: 'app',
components:{
XHeader,
Actionsheet
},
mounted:function(){
var loginStatus=window.localStorage.getItem('loginStatus');
if(loginStatus){  //目前是登录状态，修改状态数据
this.mutationIsLogin();
this.mutationLoginname(
window.localStorage.getItem('loginname')
)
this.mutationAccesstoken(
window.localStorage.getItem('accesstoken')
)
}
},
computed:{
...mapGetters(['moreMenus']),
...mapState(['isLogined',
'unLoginedMoreMenus','LoginedMoreMenus']),
isShowMore:{
get:function(){return this.$store.state.isShowMore},
set:function(nval){this.$store.commit
('mutationIsShowMore',nval)}
},

},
methods:{
...mapMutations(['mutationIsLogin','mutationLoginname','mutationAccesstoken']),
handleClickMoreMenu(menuKey, menuItem){
this.$router.push('/'+menuKey);
},

showMoreMenus(){ //xheader右侧点击后，显示actionsheet
this.isShowMore=true;
}
},
data:function(){
return{

}
}
}



